.Table {
    border: 1px solid black;
    float: left;
    overflow: scroll;
    height: 400px;
    background-color: whitesmoke;
}

.savedGraphTable {
    border-collapse: separate;
    border-spacing: 0 15px;
}

.pageDisplay {
    width: 100%;
    position: center;
    height: 400px;
    padding: 20px;
    background-color: transparent;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-45%, -50%);
}

.savedGraphPageDisplay {
    width: 100%;
    position: center;
    height: 400px;
    padding: 20px;
    background-color: transparent;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-40%, -50%);
}

.graphDisplay {
    float: left;
    min-width: 40%;
}

.graphMenu {
width: 30%;
margin-left: 500px;
margin-top: 20px;
float: left;
width: 271px;
padding: 0px 40px 0px 40px;
margin-left: 50px;
}

.saveToDB {
    border-style: groove;
}

.deleteFromDB {
    width: 30%;
    margin-left: 500px;
    margin-top: 20px;
    float: left;
    width: 271px;
    padding: 0px 40px 0px 40px;
    margin: 0px 50px;
}

#warningLabel {
    color: red;
    font-weight: bold;
}


